import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DataService } from 'src/app/services/data.service';

@Component({
  selector: 'app-solo-user',
  templateUrl: './solo-user.component.html',
  styleUrls: ['./solo-user.component.css']
})
export class SoloUserComponent implements OnInit {

  constructor(public servis:DataService,private activateRoute:ActivatedRoute) { }

  users : any

  ngOnInit(): void {
    this.activateRoute.params.subscribe(params=>{
      this.servis.getSingleUser(params["id"]).subscribe(response => {
        
        this.users = response
        this.users = [this.users.data]
        
      })
    })
  }

}
