import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { DataService } from 'src/app/services/data.service';

@Component({
  selector: 'app-forget-page',
  templateUrl: './forget-page.component.html',
  styleUrls: ['./forget-page.component.css']
})
export class ForgetPageComponent implements OnInit {

  constructor(public servis:DataService,public route:ActivatedRoute,public router:Router) { }

  ngOnInit(): void {

  }
  fepan:any
  newPassError:any
  saveNewPassForm = new FormGroup({
    password: new FormControl('',Validators.required),
  })

  saveButton(){
    this.servis.newPasswordSave(this.saveNewPassForm.value.password)
    .subscribe(response => {
      this.fepan = response
      this.fepan = this.fepan.success
      
      if (this.fepan) {
        setTimeout(() => {
          // window.location.href="/"
          this.router.navigateByUrl("/")
      }, 2500);
      }
      
    },
    err =>{
      this.newPassError = err.error.message
      console.log(this.newPassError);
      
      setTimeout(() => {
        this.newPassError = ""
      }, 2500);
    })

  }
}

