import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators, AbstractControl } from '@angular/forms';
import { DataService } from '../services/data.service';

@Component({
  selector: 'app-form',
  templateUrl: './form.component.html',
  styleUrls: ['./form.component.css']
})
export class FormComponent implements OnInit {

  constructor(
    private fb:FormBuilder,
    public servis:DataService
    ) { }

  globalForm = new FormGroup({
    
    name: new FormControl('',Validators.required),
    email: new FormControl('',Validators.required),
    no: new FormControl('',Validators.required),
    linkedin: new FormControl(''),
    alarm: new FormControl(''),
    alarmNote: new FormControl(''),

  });
  get name() { return this.globalForm.get('name') }

  // createGlobalForm(){
  //   this.globalForm = this.fb.group({
  //     name : ['',Validators.required],
  //     email : ['',Validators.required],
  //     no : ['',Validators.required],
  //     linkedin : [''],
  //     alarm : [''],
  //     note : ['']
  //   })
  // }

  ngOnInit(): void {

  }

  fepan:any
  errorMessage:any

  onSubmit(){
    this.servis.postAddCandidate(this.globalForm.value)
    .subscribe(response => {

      
      this.fepan = response
      this.fepan = this.fepan.success
      setTimeout(() => {
        this.fepan = ""
      }, 2500);
    },

    err =>{
      this.errorMessage = err.error.message
      setTimeout(() => {
        this.errorMessage = ""
      }, 2500);
    })
    console.log(this.globalForm.value);
 }

}
