import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { FormComponent } from './form/form.component';
import { CandidatesComponent } from './middle/candidates/candidates.component';
import { MiddleComponent } from './middle/middle.component';
import { SoloCandidateComponent } from './middle/solo-candidate/solo-candidate.component';
import { SoloUserComponent } from './middle/solo-user/solo-user.component';
import { usersComponent } from './middle/users/users.component';
import { ForgetPageComponent } from './right/forget-page/forget-page.component';

const routes: Routes = [
  {path:"",component : MiddleComponent},
  {path:"kullanicilar",component : usersComponent},
  {path:"kullanicilar/:id",component : SoloUserComponent},
  {path:"adaylar",component : CandidatesComponent},
  {path:"adaylar/:id",component : SoloCandidateComponent},
  {path:"form",component : FormComponent},
  {path:"api/auth/resetpassword",component : ForgetPageComponent},
  // {path:"" ,redirectTo:"",pathMatch:"full"}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
